import React, { useState, useEffect, useRef } from "react";
import "./App.css";

function ItemDone(props) {
  return (
    <li className="todo__item">
      <input
        type="checkbox"
        name="check"
        id=""
        checked={true}
        disabled={true}
      />
      {props.item.title}
    </li>
  );
}

function Item(props) {
  const [edit, setEdit] = useState(false);
  const [check, setCheck] = useState(props.item.isDone);
  const [title, setTitle] = useState("");
  const inputElement = useRef();

  return (
    <li className="todo__item">
      <input
        type="checkbox"
        name="check"
        id=""
        checked={check}
        onChange={() => {
          setCheck(!check);
          props.handleCheck(props.item, props.index);
        }}
        disabled={props.isWorking ? true : false}
      />
      {edit && (
        <input
          type="text"
          value={title}
          onChange={(e) => setTitle(e.target.value)}
          ref={inputElement}
        />
      )}
      {!edit && (
        <span
          dangerouslySetInnerHTML={{
            __html: props.item.title.replace(
              /(0[0-9\s.-]{9})/g,
              '<a href="tel:$1">$1</a>'
            ),
          }}
        ></span>
      )}
      <div className="action">
        <button
          onClick={() => {
            if (!edit) {
              setEdit(!edit);
              setTitle(props.item.title);
              props.setWorking(true);
            } else {
              setEdit(!edit);
              props.handleEdit(props.item, title, props.index);
            }
          }}
          disabled={props.isWorking && !edit ? true : false}
        >
          {edit ? "Xong" : "Sửa"}
        </button>
        <button
          onClick={() => props.handleDetele(props.item._id)}
          disabled={props.isWorking ? true : false}
        >
          Xóa
        </button>{" "}
      </div>
    </li>
  );
}

function App() {
  const [list, setList] = useState();
  const [listDone, setListDone] = useState();
  const [showListDone, setShowListDone] = useState(false);
  const [title, setTitle] = useState("");
  const [msg, setMsg] = useState();
  const [error, setError] = useState();
  const [isWorking, setWorking] = useState(false);

  const formInput = useRef();

  useEffect(() => {
    fetch("/api/todo")
      .then((rs) => (rs.status === 200 ? rs.json() : rs.text()))
      .then((rs) => (typeof rs === "string" ? setError(rs) : setList(rs)))
      .catch((e) => console.log(e));

    fetch("/api/todo?done=true")
      .then((rs) => (rs.status === 200 ? rs.json() : rs.text()))
      .then((rs) => (typeof rs === "string" ? setError(rs) : setListDone(rs)))
      .catch((e) => console.log(e));
  }, []);

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!title.trim().length) {
      setMsg("Nhập công việc trước bạn ei!");
      return;
    }

    setWorking(true);

    fetch("/api/todo", {
      method: "POST",
      body: JSON.stringify({ title }),
      headers: {
        "Content-Type": "application/json",
      },
      credentials: "same-origin",
    })
      .then((rs) => (rs.status === 200 ? rs.json() : rs.text))
      .then((data) => {
        if (typeof data === "string") {
          setError(data);
          return;
        }
        setList([
          {
            _id: data._id,
            time: data.time,
            title: data.title,
            isDone: data.isDone,
          },
          ...list,
        ]);
        setTitle("");
        setMsg();
        setError();
        setWorking(false);
      })
      .catch((e) => {
        console.log(e);
        setWorking(false);
      });
  };

  const handleDetele = (id) => {
    setWorking(true);
    fetch("/api/todo", {
      method: "DELETE",
      body: JSON.stringify({ id }),
      headers: {
        "Content-Type": "application/json",
      },
      credentials: "same-origin",
    })
      .then((rs) => {
        rs.status === 200
          ? setList(list.filter((item) => item._id !== id))
          : setError(true);
        setWorking(false);
      })
      .catch((e) => {
        console.log(e);
        setWorking(false);
      });
  };

  const handleCheck = (item) => {
    setWorking(true);
    fetch("/api/todo", {
      method: "PUT",
      body: JSON.stringify({
        id: item._id,
        title: item.title,
        isDone: !item.isDone,
      }),
      headers: {
        "Content-Type": "application/json",
      },
      credentials: "same-origin",
    }).then((rs) => {
      if (rs.status === 200) {
        setList(list.filter((listItem) => listItem._id !== item._id));
        setListDone([{ ...item, timeDone: Date.now() }, ...listDone]);
        setError();
        setWorking(false);
      } else {
        setError("Úi lỗi rồi!");
        setWorking(false);
      }
    });
  };

  const handleEdit = (item, title, index) => {
    fetch("/api/todo", {
      method: "PUT",
      body: JSON.stringify({
        id: item._id,
        title: title,
        isDone: item.isDone,
      }),
      headers: {
        "Content-Type": "application/json",
      },
    }).then((rs) => {
      if (rs.status === 200) {
        const newList = [...list];
        newList[index].title = title;
        setList(newList);
        setError();
        setWorking(false);
      } else {
        setError("Úi lỗi rồi!");
        setWorking(false);
      }
    });
  };

  return (
    <div className="App">
      <h1>TODO App</h1>
      {error && <p className="error">Úi, lỗi rồi!</p>}
      <form
        action=""
        method="POST"
        className="todo__form"
        onSubmit={(e) => handleSubmit(e)}
      >
        <input
          type="text"
          name="title"
          id=""
          value={title}
          onChange={(e) => setTitle(e.target.value)}
          ref={formInput}
          placeholder="Công việc hôm nay của bạn là gì?"
          disabled={isWorking ? true : false}
        />
      </form>
      <ul className="todo__list">
        {list &&
          list.length > 0 &&
          list
            .sort((a, b) => b.time - a.time)
            .map((item, index) => (
              <Item
                key={item._id}
                item={item}
                index={index}
                handleCheck={handleCheck}
                handleDetele={handleDetele}
                handleEdit={handleEdit}
                isWorking={isWorking}
                setWorking={setWorking}
              />
            ))}

        {list && list.length === 0 && (
          <li
            style={{ textAlign: "center" }}
            onClick={() => formInput.current.focus()}
          >
            {msg ? msg : "Thêm một công việc để bắt đầu nào!"}
          </li>
        )}
        {!list && <li style={{ textAlign: "center" }}>Đang tải...</li>}
      </ul>
      <ul className="todo__list--done">
        <li
          onClick={() => setShowListDone(!showListDone)}
          style={{ textAlign: "center", marginBottom: "20px" }}
        >
          Xem các mục đã hoàn thành
        </li>
        {showListDone &&
          listDone.length > 0 &&
          listDone
            .sort((a, b) => b.timeDone - a.timeDone)
            .map((item) => {
              return [<ItemDone key={item._id} item={item} />];
            })}
        {showListDone && listDone.length === 0 && (
          <li style={{ textAlign: "center", color: "#999999" }}>
            Chưa hoàn thành công việc nào!
          </li>
        )}
      </ul>
    </div>
  );
}

export default App;
